@media all {
    *
    {
        margin: 0;
        padding: 0;
    }
    
    body
    {
        background-color: black;
    }
    
    img {
      filter: grayscale(100%) brightness(75%);
    }
    
    h3.highlight:hover {
      color: white;
    }
    
    img.highlight:hover {
      filter: brightness(100%);
    }
    
    i.highlight:hover {
      border: solid white;
      border-width: 0 3px 3px 0;
      transition: none;
    }
    
    i.highlight:hover::after {
      border: solid silver;
      border-width: 0 3px 3px 0;
      transition: none;
    }
    
    .clickable {
      cursor: pointer;
    }
    
    .menu {
      position: fixed;
      font-size: 12.5px;
      font-family: Hack, sans-serif;
      color: silver;
      letter-spacing: 1px;
      height: 2.5%;
      width: 50%;
      z-index: 10;
      top: 5%;
      opacity: 0;
      -webkit-animation:fade-in 2s forwards;
      -webkit-animation-delay:4s;
    }
    
    .menu h3{
      width: fit-content;
      cursor: pointer;
      margin-left: auto;
        margin-right: auto;
    }
    
    .seperate{
      float: left;
      display: inline;
      position: relative;
      width: 25%;
      height: 100%;
      text-align: center;
    }
    .section
    {
      display: flex;
      align-items: center;
      position: relative;
      font-weight: 700;
      text-align: center;
      font-size: 20px;
      font-family: Hack, sans-serif;
      letter-spacing: 5px;
      text-transform: uppercase;
      color: white;
    }
    
    .introduction {
        height: 100%;
        width: 75%;
        position: fixed;
        z-index: 2;
        overflow-x: hidden;
        background-image: linear-gradient(to right, black, black, black, transparent);
        transition: 0.5s all ease;
      }
    
      .introduction.fadeOut{
        opacity: 0;
      }
    
    /*
    * credit for welcome animation @https://codepen.io/grohit/pen/mdJqEzK
    */ 
    .welcome {
      top: 50%;
      left: 8%;
      background: linear-gradient(90deg, #000, #fff, #000);
      -webkit-background-clip: text;
      background-clip: text;
      -webkit-text-fill-color: transparent;
      background-repeat: no-repeat;
      background-size: 80%;
      animation-delay: 1s;
      animation: shine 3s linear forwards;
      position: fixed;
    }
    
    @keyframes shine {
      0% {
        background-position-x: -500%;
      }
      100% {
        background-position-x: 500%;
      }
    }
    
    .name {
      width: 50%;
        top: 45%;
        text-align: center;
        position: fixed;
        opacity: 0;
    }
    
    .role {
      font-size: 18px;
      color: lightgray;
    }
    
    /* .name img {
      height: 7%;
      left: 23%;
      text-align: center;
      position: fixed;
      z-index: 3;
      top: 90%;
      max-width: auto;
      max-height: 100%;
      -webkit-animation: bounce 2s infinite ease;
    } */
    
    /* .name.startFade img {
      -webkit-animation: bounce 2s infinite ease;
    } */
    
    @keyframes bounce {
      0%   { transform: translateY(0); }
      50%  { transform: translateY(-15px); }
      100% { transform: translateY(0); }
    }
    
    
    .name.startFade {
      opacity: 0;
      -webkit-animation:fade-in 2s forwards;
      -webkit-animation-delay:4s;
    }
    
    .name.fadeIn {
      -webkit-animation:fade-in 1s forwards;
    }
    
    .name.fadeOut {
      -webkit-animation:fade-out 0.5s forwards;
    }
    
    @keyframes fade-in {
      0% {opacity:0;}
      100% {opacity:1;}
    } 
    @keyframes fade-out {
      0% {opacity:1;}
      100% {opacity:0;}
    } 
    
    .aboutMe {
      width: 50%;
      
    }
    
    .about {
      opacity: 0;
      width: 50%;
      left: 25%;
      top: 35%;
      text-align: center;
      position: fixed;
      z-index: 1;
      overflow-x: hidden;
    }
    
    .about.fadeIn {
      transition: 1s all ease;
      opacity: 1;
    }
    
    .about.fadeOut {
      transition: 0.5s all ease;
      opacity: 0;
    }
    
    .description {
      opacity: 0;
      z-index: 2;
    }
    
    .description p {
      left: 5%;
      top: 45%;
      height: 30%;
      width: 40%;
      text-align: center;
      position: fixed;
      z-index: 1;
      letter-spacing: 1px;
      color: lightgray;
      font-size: 20px;
      font-weight: 550;
    }
    
    .description.fadeIn {
      transition: 1s all ease;
      opacity: 1;
    }
    
    .description.fadeOut {
      transition: 0.5s all ease;
      opacity: 0;
      pointer-events: none;
      
    }
    
    .social {
      height: 3%;
      left: 22.5%;
      text-align: center;
      position: fixed;
      z-index: 3;
      top: 91%;
      max-width: auto;
      max-height: 100%;
    }
    
    .social a img{
      max-width: auto;
      max-height: 100%;
      z-index: 1000;
    }
    
    .social a{
      text-decoration: none;
      padding-left: 10px;
      padding-right: 10px;
    }
    
    .social.stopLink  a{
      opacity: 0;
      position: fixed;
      pointer-events: none;
    }

    .aboutSkills {
      position: fixed;
      top: 35%;
      right: 15%;
      height: 50%;
      width: 50%;
    }

    .aboutSkills img{
      max-width: auto;
      max-height: 100%;
    }
    
    .projects {
    }
    
    .projectName {
      opacity: 0;
      width: 50%;
      top: 45%;
      text-align: center;
      position: fixed;
      z-index: 1;
      overflow-x: hidden;
    }
    
    .projectName.fadeIn {
      transition: 2s all ease;
      opacity: 1;
    }
    
    .projectName.fadeOut {
      transition: 0.5s all ease;
      opacity: 0;
    }
    .git {
      height: 3%;
      left: 5%;
      text-align: center;
      position: fixed;
      z-index: 3;
      top: 91%;
      max-width: auto;
      max-height: 100%;
    }
    .git img{
      max-width: auto;
      max-height: 100%;
      position: relative;
    }
    
    .git.stopLink  a{
      opacity: 0;
      position: fixed;
      pointer-events: none;
    }
    
    .skills {
      height: 3%;
      left: 32%;
      width: 20%;
      text-align: center;
      position: fixed;
      z-index: 2;
      top: 91%;
    }
    
    
    .skills img{
      max-width: auto;
      max-height: 100%;
    }
    
    .skills.fadeIn {
      transition: 2s all ease;
      opacity: 1;
    }
    
    .skills.fadeOut {
      transition: 0.5s all ease;
      opacity: 0;
    }
    
    .projectDescription {
      opacity: 0;
      height: 50%;
      width: 50%;
      left: 5%;
      top: 42.5%;
      position: fixed;
      text-align: center;
      position: fixed;
      z-index: 2;
      overflow-x: hidden;
    }
    
    
    .projectDescription.fadeIn {
      transition: 2s all ease;
      opacity: 1;
      
    }
    
    .projectDescription.fadeOut {
      transition: 0.25s all ease;
      opacity: 0;
    }
    
    .project {
      opacity: 0;
      width: 40%;
      position: fixed;
      z-index: 15;
    }
    
    .project p{ 
      letter-spacing: 0px;
      font-size: 20px;
      font-weight: 100;
      text-transform: none;
      text-align: left;
      left: 20%;
      color: silver
    }
    .project h3{
      font-size: 30px;
      letter-spacing: 1px;
      text-align: left;
    }
    
    .project.fadeIn {
      transition: 0.5s all ease;
      opacity: 1;
    }
    
    .project.fadeOut {
      transition: 0.5s all ease;
      opacity: 0;
    }
    
    .contact {
    }
    
    .nextBtn {
      right: 2.5%;
      -webkit-transform: rotate(-45deg) scale(2);
    }
    
    .prevBtn {
      left: 55%;
      -webkit-transform: rotate(135deg) scale(2);
    }
    
    .arrow {
      top: 50%;
      cursor: pointer;
      position: fixed;
      border: solid silver;
      border-width: 0 3px 3px 0;
      display: inline-block;
      z-index: 10;
      padding: 3px;
    }
    
    .arrow.fadeIn {
      transition: 2s all ease;
      opacity: 1;
    }
    
    .arrow.fadeOut {
      transition: 0.5s all ease;
      cursor: default;
      opacity: 0;
    }
    
    .webgl {
      position: fixed;
      -webkit-animation:fade-in 10s forwards;
      top: 0;
      left: 0;
      outline: none;
    }
    
    }
    
